.content {
  width: 100%;
  height: 100%;
}

.shoppingColum{
  padding: 0;
  display: flex;
  position: sticky;
  width: 100%;
  top:2vh;
  margin-top: 2vh;
  margin-bottom: 0vh;
  text-align: center;
}

.shoppingColum>li{
  text-decoration: underline;
  list-style: none;
  padding: 0.7% 0;
  border-radius: 1em;
  background-color: #FFD3B4;
  font-weight: bold;
}

.shoppingColum>li:nth-child(1){
  margin-right: 0.5vw;
  width: 32vw;
}

.shoppingColum>li:nth-child(2),.shoppingColum>li:nth-child(3){
  margin-right: 0.5vw;
  width: 19vw;
}

.shoppingColum>li:nth-child(4){
  width: 19.5vw;
}

.topic{
  padding: 0;
}

.topic>li{
  list-style: none;
  padding: 0 1vw;
  height: 6.5vh;
  margin-bottom: 1%;
  display: flex;
  align-items: center;
}

.topic>li>input:nth-child(1){
  font-size: 80%;
  margin-right: 3vw;
  padding-left: 2vw;
  padding-right: 2vw;
  width: 25.2vw;
  height: 80%;
  border-radius: 2em;
}

.topic>li>input:nth-child(2),.topic>li>input:nth-child(3){
  font-size: 80%;
  font-weight: 100;
  margin-right: 2.5vw;
  width: 16.5vw;
  height: 80%;
  border-radius: 2em;
  text-align: center;
}

.topic>li>input:nth-child(4){
  font-size: 75%;
  margin-right: 0.3vw;
  width: 8vw;
  height: 80%;
  border-radius: 2em;
  text-align: center;
}

.topic>li>select{
  font-size: 65%;
  background-color:unset;
  margin-right: 1vw;
  width: 7vw;
  height: 80%;
  border: none;
}

.topic>li:nth-child(odd){
  background-color: #ffd3b460;
}

.addRow{
  margin: 0 1vw;
  text-align: center;
  font-size: 160%;
  border-radius: 20px;
  background-color: #D5ECC2;
}

.addRow:hover{
  transform: translate(0, 3px);
  box-shadow: 0 3px 0 #98DDCA;
}

.addRow:active{
  transform: translate(0, 0);
  box-shadow: none;
}